.Presentation{
  background-color: #6f4a37;
  height: 70vh;
}

.pic-presentation {
  height: 40vh;
  pointer-events: none;
  padding-right:10vmin;
}

.Presentation-general {
  text-align: left;

  height : 40vh;
  padding : 1vmin;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.Presentation-other {
  padding : 1vmin;
  color: white;
}

.text-presentation-general{
  font-size: 2.5vmin;
  max-width: 50%;
}

.text-presentation-other{
  font-size: 2.5vmin;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media all  and (orientation: portrait) {
  /* applies to mobiles in portrait mode */

  .Presentation {
    height: 72vh;
  }

  .Presentation-general {
    height : 45vh;
    display: block;
  }



  .text-presentation-general{
    margin-top: 4vh;
    font-size: calc(1vh + 1vw);
    max-width: 90vw;

  }

  .text-presentation-other{
    font-size: calc(1vh + 1vw);
    max-width: 80%;
  }

  .pic-presentation {
    height: 20vh;
    pointer-events: none;
    margin-top: 2vh;
    display: block;
    margin-left: auto;
    margin-right: auto
  }
}